<template>
  <div id="container">
    <div id="profile">
      <div class="user-pic">
        <img :src="photo" :alt="first_name">
      </div>
      <div class="user-name">{{ this.first_name }} {{ this.last_name }}</div>
      <div class="user-tickets-count">
        <ion-icon name="ticket-outline" class="ai-icon"></ion-icon> {{ tickets_total }}
      </div>
      <div class="user-btn-tools">
        <router-link to="/my-tickets" tag="div" class="user-tickets-btn btn ">Мои тикеты
          <ion-icon name="chevron-forward-outline" class="arrow-right-icon"></ion-icon></router-link>
        <router-link to="/participation" tag="div" class="offers-for-user btn">Повысить шансы
          <ion-icon name="chevron-forward-outline"class="arrow-right-icon"></ion-icon></router-link>
      </div>
    </div>

    <BottomNavigationMenu active="profile" />
  </div>
</template>

<script>
import HeaderUserBar from "@/components/HeaderUserBar";
import BottomNavigationMenu from "@/components/BottomNavigationMenu";
import axios from "axios";
export default {
  name: "Profile",
  data: () => ({
    user_id: 0,
    photo: '',
    first_name: '',
    last_name: '',
    tickets_total: 0,
  }),
  components: {
    HeaderUserBar,
    BottomNavigationMenu
  },
  mounted() {
    const userData = window.Telegram.WebApp.initDataUnsafe.user;

    this.user_id = userData.id

    axios.get('https://back.sombra.uz/api/user/' + this.user_id)
    .then(response => {
      if (response.data.data) {
        this.photo = response.data.data.photo
        this.first_name = response.data.data.first_name
        this.last_name = response.data.data.last_name
      }
    })
    .catch(error => console.log(error));

    axios.get('https://back.sombra.uz/api/ticket/' + this.user_id)
        .then(response => {
          if (response.data.data) {
            this.tickets_total = response.data.total
          }
        })
        .catch(error => console.log(error));
  }
}
</script>

<style scoped>
