  #container {
    margin-bottom: -10px !important;
    position: relative;
    top: -90px;
  }
  #profile {
    margin-top: 150px !important;
  }
}

@media screen and (min-height: 700px) {
  #container {
    margin-bottom: -10px;
    position: relative;
    top: -500px;
  }
  #profile {
    margin-top: 150px !important;
  }
}

@media screen and (min-height: 652px) {
  #container {
    margin-bottom: -5px !important;
    position: relative;
    top: -100px;
  }
  #profile {
    margin-top: 110px;
  }
}

@media screen and (min-height: 498px) {
  #container {
    margin-bottom: -5px;
    position: relative;
    top: -100px;
  }
  #profile {
    margin-top: 110px;
  }
}
</style>